export type UserRole = 'consumer' | 'repair_shop' | 'insurance_company' | 'carfax_service';

export interface SignupRequest {
  role: UserRole;
  phone_number?: string;
  address?: string;
  city?: string;
  state?: string;
  zip_code?: string;
  insurance_company_id?: number;
  carfax_api_key?: string;
}

export interface User {
  id: number;
  firebase_uid: string;
  email: string;
  name: string;
  role: UserRole;
  phone_number?: string;
  address?: string;
  city?: string;
  state?: string;
  zip_code?: string;
  created_at: string;
  updated_at: string;
}

export interface AuthResponse {
  success: boolean;
  message: string;
  data?: User;
  authenticated?: boolean;
  user_exists?: boolean;
  firebase_user?: {
    uid: string;
    email: string;
    name: string;
    email_verified?: boolean;
    picture?: string | null;
  };
}

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:8000';

export class AuthService {
  private static async getAuthHeader(): Promise<string> {
    const { auth } = await import('./firebase');
    const user = auth.currentUser;
    if (!user) throw new Error('No authenticated user');
    const token = await user.getIdToken();
    return `Bearer ${token}`;
  }

  static async signup(data: SignupRequest): Promise<AuthResponse> {
    const authHeader = await this.getAuthHeader();
    const response = await fetch(`${API_BASE_URL}/api/auth/signup`, {
      method: 'POST',
      headers: {
        'Authorization': authHeader,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.message || 'Signup failed');
    }

    return response.json();
  }

  static async login(): Promise<AuthResponse> {
    const authHeader = await this.getAuthHeader();
    const response = await fetch(`${API_BASE_URL}/api/auth/login`, {
      method: 'POST',
      headers: {
        'Authorization': authHeader,
      },
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.message || 'Login failed');
    }

    return response.json();
  }

  static async refresh(): Promise<AuthResponse> {
    const authHeader = await this.getAuthHeader();
    const response = await fetch(`${API_BASE_URL}/api/auth/refresh`, {
      method: 'POST',
      headers: {
        'Authorization': authHeader,
      },
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.message || 'Refresh failed');
    }

    return response.json();
  }

  static async logout(): Promise<AuthResponse> {
    const authHeader = await this.getAuthHeader();
    const response = await fetch(`${API_BASE_URL}/api/auth/logout`, {
      method: 'POST',
      headers: {
        'Authorization': authHeader,
      },
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.message || 'Logout failed');
    }

    return response.json();
  }
}

